<template>
  <div class="goodlist-wrapper">
    <div class="list-header van-hairline--top-bottom">
      <div @click="changeType('all')" :class="{ active: type === 'all' }">
        综合
      </div>
      <div @click="changeType('sale')" :class="{ active: type === 'sale' }">
        销量
      </div>
      <div
        @click="changeType('')"
        class="price"
        :class="{
          'price-up': type === 'price-up',
          'price-down': type === 'price-down',
        }"
      >
        价格
      </div>
    </div>
    <div class="list-content">
      <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
        <van-list
          v-model="loading"
          :finished="finished"
          finished-text="没有更多了"
          @load="onLoad"
          :immediate-check="false"
        >
          <GoodsCard
            v-for="good in goodList"
            :key="good.id"
            v-bind="good"
            :num="counterMap[good.id]"
          />
        </van-list>
      </van-pull-refresh>
    </div>
  </div>
</template>

<script>
import { mapActions, mapMutations, mapState } from 'vuex';
import { v4 as uuid } from 'uuid';
import GoodsCard from './GoodsCard.vue';

export default {
  components: {
    GoodsCard,
  },
  data() {
    return {
      type: 'all',
      page: 1,
      refreshing: false,
      loading: false,
      finished: false,
      uuid,
    };
  },
  computed: {
    ...mapState({
      goodList: (state) => state.goodList,
      counterMap: (state) => state.counterMap,
    }),
  },
  methods: {
    ...mapMutations(['resetGoodList', 'setCounterMap']),
    ...mapActions(['getGoodList']),
    changeType(type) {
      if (type === 'all') {
        this.type = 'all';
      } else if (type === 'sale') {
        this.type = 'sale';
      } else if (this.type === 'price-up') {
        this.type = 'price-down';
      } else {
        this.type = 'price-up';
      }
      this.onRefresh();
    },
    async onRefresh() {
      this.refreshing = true;
      this.finished = false;
      this.loading = false;
      this.page = 1;
      this.resetGoodList();
      await this.getGoodList({ page: 1, sort: this.type });
      this.refreshing = false;
    },
    async onLoad() {
      this.page += 1;
      const result = await this.getGoodList({
        page: this.page,
        sort: this.type,
      });
      this.loadig = false;
      if (result) {
        this.loading = false;
      } else {
        this.finished = true;
      }
    },
  },
};
</script>

