<template>
  <div class="classify-wrapper">
    <router-link class="search-btn" tag="div" to="/search">
      <van-icon name="search" />
      <div>苹果特价一元两斤</div>
    </router-link>
    <OneTab />
    <template v-if="!isLoading"
      ><SideBar />
      <template v-if="show"> <GoodList /> </template>
      <van-loading v-else-if="!show" type="spinner" size="2rem" vertical />
      /></template
    >
    <van-loading v-else type="spinner" size="2rem" vertical />
  </div>
</template>

<script>
import { mapActions, mapMutations, mapState } from 'vuex';
import OneTab from '../components/OneTab.vue';
import SideBar from '../components/SideBar.vue';
import GoodList from '../components/GoodList.vue';

export default {
  components: {
    OneTab,
    SideBar,
    GoodList,
  },
  methods: {
    ...mapMutations(['resetGoodList']),
    ...mapActions(['getGoodList']),
  },
  computed: {
    ...mapState({
      isLoading: (state) => state.isLoading,
      show: (state) => state.show,
      sideList: (state) => state.sideList,
    }),
  },
  watch: {
    isLoading() {
      if (!this.isLoading) {
        this.resetGoodList();
        this.getGoodList({ type: this.sideList[0], page: 1, sort: 'all' });
      }
    },
  },
};
</script>

<style lang="less" scoped>
.classify-wrapper {
  width: 375px;
  .search-btn {
    width: 355px;
    height: 33px;
    line-height: 33px;
    background-color: #eee;
    margin: 11px auto 0;
    border-radius: 10px;
    font-size: 14px;
    text-align: center;
    color: #a1a1a1;
    > * {
      display: inline-block;
      vertical-align: middle;
    }
  }
}
</style>
