<template>
  <div class="search-wrapper">
    <div class="search-head">
      <van-icon name="arrow-left" class="arr-left" @click="$router.goBack()" />
      <van-search
        class="search-content"
        v-model="value"
        show-action
        :placeholder="place"
        @search="onSearch"
        @input="input"
        @focus="focus"
      >
        <template #action v-if="showLikeList">
          <div @touchend="onSearch(value)">搜索</div>
        </template>
        <template #action v-else>
          <van-icon
            name="cart-o"
            id="shop-car"
            :badge="badge"
          ></van-icon>
        </template>
      </van-search>
    </div>
    <div class="like-search" v-if="likeList.length > 0 && showLikeList">
      <van-list>
        <van-cell v-for="item in likeList" :key="item" @click="onSearch(item)">
          <span v-html="fontColor(item)"></span>
        </van-cell>
      </van-list>
    </div>
    <div class="good-list" v-if="!showLikeList">
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
        :immediate-check="false"
      >
        <GoodsCard
          v-for="good in goodList"
          :key="good.id"
          v-bind="good"
          :num="counterMap[good.id]"
        />
      </van-list>
    </div>
    <div class="history" v-if="likeList.length <= 0 && showLikeList">
      <MyHistory :searchList="searchList" @search="onSearch" />
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import GoodsCard from '../components/GoodsCard.vue';
import MyHistory from '../components/MyHistory.vue';

export default {
  components: {
    GoodsCard,
    MyHistory,
  },
  data() {
    return {
      value: this.place,
      place: '酒',
      likeList: [],
      timer: null,
      loading: false,
      finished: false,
      page: 1,
      size: 6,
      goodList: [],
      showLikeList: true,
      total: 0,
      searchList: [],
    };
  },
  created() {
    this.searchList = JSON.parse(localStorage.getItem('searchList')) || [];
  },
  computed: {
    ...mapState({
      counterMap: (state) => state.counterMap,
    }),
    badge() {
      const num = Object.values(this.counterMap).reduce((prev, next) => prev + next, 0);
      if (num > 99) {
        return '99+';
      }
      return num;
    },
  },
  methods: {
    async onLoad() {
      this.showLikeList = false;
      const value = await this.$api.search(this.value, this.page, this.size);
      this.goodList = [...this.goodList, ...value.list];
      this.total = value.total;
      if (this.goodList.length >= this.total) {
        this.finished = true;
      } else {
        this.page += 1;
        this.loading = false;
      }
    },
    fontColor(item) {
      const reg = new RegExp(this.value, 'g');
      return item.replace(reg, this.value.fontcolor('red'));
    },
    onSearch(val) {
      if (!val) {
        this.value = this.place;
      } else {
        this.value = val;
      }
      // 搜索记录按时间排序及本地存储
      const result = this.searchList.find((item) => item.value === this.value);
      if (result) {
        result.time = new Date().getTime();
        this.searchList.sort((a, b) => b.time - a.time);
      } else {
        this.searchList.unshift({ value: this.value, time: new Date().getTime() });
        if (this.searchList.length > 10) {
          this.searchList.pop();
        }
      }
      localStorage.setItem('searchList', JSON.stringify(this.searchList));

      this.likeList = [];
      this.page = 1;
      this.finished = false;
      this.onLoad();
    },
    async input() {
      // 防抖
      if (this.value === '') {
        // 输入最终结果为空，直接阻止发送
        this.likeList = [];
        return;
      }
      if (this.timer) {
        // 输入过程中截掉定时器以发送请求
        clearTimeout(this.timer);
        this.timer = null;
      } else {
        // 停止输入后0.3s发送请求
        this.timer = setTimeout(async () => {
          const value = await this.$api.likeSearch(this.value);
          this.likeList = value.result;
          clearTimeout(this.timer);
          this.timer = null;
        }, 300);
      }
    },
    focus() {
      this.showLikeList = true;
    },
  },
};
</script>

