<template>
  <div class="sidebar-wrapper" ref="side">
    <div
      v-for="(item, i) in sideList"
      :key="item"
      :class="{ active: index == i }"
      @touchend="scrollTo(i, $event)"
      @touchstart="move = false"
      @touchmove="move = true"
    >
      {{ i === 0 ? "全部" : item }}
    </div>
  </div>
</template>

<script>
import { mapActions, mapMutations, mapState } from 'vuex';
import util from '../util/tool';

export default {
  data() {
    return {
      index: 0,
      move: false,
    };
  },
  async created() {
    // this.resetGoodList();
    // await this.getGoodList({ type: this.sideList[0], page: 1, sort: 'all' });
    this.setShow(true);
  },
  computed: {
    ...mapState({
      sideList: (state) => state.sideList,
    }),
  },
  methods: {
    ...mapActions(['getGoodList']),
    ...mapMutations(['resetGoodList', 'setShow']),
    async scrollTo(i, e) {
      if (this.move) {
        return;
      }
      this.index = i;
      const { side } = this.$refs;
      const sonTop = e.target.getBoundingClientRect().top;
      const sonHeight = e.target.offsetHeight;
      const pTop = side.getBoundingClientRect().top;
      const pHeight = side.offsetHeight;
      util.moveTo(side.scrollTop, sonTop + sonHeight / 2 - pTop - pHeight / 2, side, 'scrollTop');
      this.resetGoodList();
      this.setShow(false);
      await this.getGoodList({ page: 1, sort: 'all', type: this.sideList[i] });
      this.setShow(true);
    },
  },
};
</script>

<style lang="less" scoped>
.sidebar-wrapper {
  position: fixed;
  left: 0;
  top: 135px;
  bottom: 50px;
  width: 79px;
  background: #f8f8f8;
  overflow: auto;
  div {
    width: 79px;
    text-align: center;
    height: 40px;
    line-height: 40px;
    position: relative;
  }
  .active {
    font-weight: bold;
    color: #ff1a90;
  }
  .active::before {
    position: absolute;
    width: 6px;
    height: 18px;
    background: #ff1a90;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    content: "";
  }
}
.sidebar-wrapper::-webkit-scrollbar {
  width: 0;
  background: none;
}
</style>
