import Vue from 'vue';
import Vuex from 'vuex';
import api from '../api/index';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    sideList: [],
    isLoading: false,
    size: 7,
    goodList: [],
    type: null,
    show: false,
    counterMap: {},
  },
  mutations: {
    setSideList(state, list) {
      state.sideList = list;
    },
    setIsLoading(state, bool) {
      state.isLoading = bool;
    },
    setGoodList(state, list) {
      state.goodList = [...state.goodList, ...list];
    },
    resetGoodList(state) {
      state.goodList = [];
    },
    setGoodsType(state, type) {
      state.type = type;
    },
    setShow(state, bool) {
      state.show = bool;
    },
    setCounterMap(state, map) {
      state.counterMap = map;
    },
    setStorageChange(state, { id, value }) {
      if (state.counterMap[id]) {
        if ((value === -1 && state.counterMap[id] === 1) || value === -Infinity) {
          Vue.delete(state.counterMap, id);
        } else {
          Vue.set(state.counterMap, id, state.counterMap[id] + value);
        }
      } else {
        Vue.set(state.counterMap, id, 1);
      }
      localStorage.setItem('goods', JSON.stringify(state.counterMap));
    },
  },
  actions: {
    async getSideList({ commit }, type) {
      commit('setIsLoading', true);
      const res = await api.getSideList(type);
      commit('setSideList', res);
      commit('setIsLoading', false);
    },
    async getGoodList({ state, commit }, options) {
      const { page, sort } = options;
      const type = options.type || state.type;
      const { list, total } = await api.getGoodList(type, page, state.size, sort);
      commit('setGoodList', list);
      commit('setGoodsType', type);
      if (total > state.goodList.length) {
        return true;
      }
      return false;
    },
  },
  modules: {
  },
});
