import axios from 'axios';

const instance = axios.create({
  baseURL: 'https://mallapi.duyiedu.com/goods/',
  params: {
    appkey: 'wutao_1644812143938',
  },
});

instance.interceptors.response.use((val) => val.data);

export default {
  getSideList(type) {
    return instance.get('/getsidebar', { params: { type } });
  },
  getGoodList(type, page, size, sort) {
    return instance.get('/getGoodsList', {
      params: {
        type, page, size, sort,
      },
    });
  },
  likeSearch(value) {
    return instance.get('/likeSearch', { params: { likeValue: value } });
  },
  search(type, page, size) {
    return instance.get('/search', { params: { type, page, size } });
  },
  getGoodsByIds(value) {
    return instance.get('/getGoodsByIds', { params: { value } });
  },
};
