<template>
  <div class="shopping">
    <div class="top-nav">
      <van-nav-bar title="购物车" right-text="删除" @click-right="del" />
    </div>
    <div class="card-list">
      <template>
        <van-checkbox-group
          v-model="result"
          ref="checkboxGroup"
          v-show="JSON.stringify(counterMap) !== '{}'"
        >
          <div class="card-box" v-for="item in list" :key="item.id">
            <van-checkbox class="check" :name="item.id"></van-checkbox>
            <GoodsCard v-bind="item" :num="counterMap[item.id]" :nofly="true" />
          </div>
        </van-checkbox-group>
      </template>
      <div v-show="JSON.stringify(counterMap) === '{}'" class="empty-car">
        购物车空空如也,快去选购吧!
      </div>
    </div>
    <van-submit-bar
      :price="allMoney"
      :button-text="`去结算(${totalNum})`"
      @submit="onSubmit"
    >
      <van-checkbox v-model="checked" @click="checkAll">全选</van-checkbox>
    </van-submit-bar>
  </div>
</template>

<script>
import { mapMutations, mapState } from 'vuex';
import { Dialog, Toast } from 'vant';
import GoodsCard from '../components/GoodsCard.vue';

export default {
  components: {
    GoodsCard,
  },
  data() {
    return {
      result: [],
      list: [],
      checked: false,
    };
  },
  created() {
    this.getAllData();
  },
  computed: {
    ...mapState({
      counterMap: (state) => state.counterMap,
    }),
    allMoney() {
      const restArr = this.list.filter((item) => this.result.includes(item.id));
      return restArr.reduce((prev, next) => {
        const num = this.counterMap[next.id] || 0;
        return Math.round(num * next.price * 100) + prev;
      }, 0);
    },
    totalNum() {
      const restArr = this.list.filter((item) => this.result.includes(item.id));
      const res = restArr.reduce((prev, next) => prev + (this.counterMap[next.id] || 0), 0);
      return res;
    },
  },
  watch: {
    result() {
      if (this.result.length === this.list.length) {
        this.checked = true;
      } else {
        this.checked = false;
      }
    },
  },
  methods: {
    ...mapMutations(['setStorageChange']),
    checkAll() {
      if (this.checked) {
        this.$refs.checkboxGroup.toggleAll(true);
      } else {
        this.$refs.checkboxGroup.toggleAll(false);
      }
    },
    onSubmit() {},
    async del() {
      if (this.result.length === 0) {
        Toast('你没有选中任何商品!');
        return;
      }
      try {
        await Dialog.confirm({ message: '确认删除选中商品吗？' });

        this.result.forEach((id) => {
          if (this.counterMap[id]) {
            this.setStorageChange({ id, value: -Infinity });
          }
          this.list = this.list.filter((item) => !this.result.includes(item.id));
        });
      } catch (error) {
        Toast('用户点击了取消!');
      }
    },
    async getAllData() {
      const result = Object.keys(this.counterMap);
      const res = await this.$api.getGoodsByIds(result.join());
      this.list = res.list;
    },
  },
};
</script>

<style lang="less" scoped>
.shopping {
  background: #eee;
  min-height: 100vh;
  .top-nav {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 10;
  }
  .card-list {
    width: 100%;
    position: absolute;
    top: 46px;
    z-index: 0;
    padding: 10px 10px 100px 10px;
    background: #fff;
    box-sizing: border-box;
    .card-box {
      display: flex;
      justify-content: center;
    }
    .check {
      margin-right: 10px;
      flex-shrink: 0;
    }
    .empty-car {
      height: 100px;
      line-height: 100px;
      font-size: 16px;
      text-align: center;
    }
  }
  .van-submit-bar {
    bottom: 51px;
  }
}
</style>
